.popup{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0,0,0,0.5);

}
.popup-in{
    position: relative;
    background-color: #b5dddf ;
    width: 70%;
    padding:15px;
    min-height: 200px;
    max-height: 500px;
    border-radius: 15px;
    flex-direction: column;
    display: flex;
    justify-content: space-between;
    /* overflow: scroll; */
    /* b5dddf */
    animation: popup 0.2s ease-in-out;

}
@keyframes popup{
    0%{
        transform: scale(0.5)
    }
    
}
.close{
    position: absolute;
    right:-12px;
    top:-12px;
    border-radius: 50%;
    border: none;
    background: transparent;
    background-color: rgba(0,0,0,0.6);
    color: white;
    cursor: pointer;
    z-index: 100;
}
table{
    border-radius: 5px;
    background-color: hsl(232, 48%, 26%);
    color: #b5dddf;
    text-align: center;
    width: 100%;
}


td{
padding: 5px;
border-bottom: 1px solid #b5dddf;
}
th{
padding: 5px;
border-bottom: 3px solid #b5dddf;
}
.order{
    margin-top: 20px;
    padding: 10px;
    background-color: hsl(232, 48%, 26%);
    color: #b5dddf;
    font-weight: 600;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    
}

.delete{
    cursor: pointer;
}

