.mxw-head{
    max-width: 1200px;
    margin: auto;
    color: white;
    width: 100%;
    align-items: center;
    padding: 0 10px;

}
.header{
    /* background: rgb(36,163,181);
    background: linear-gradient(90deg, rgba(36,163,181,1) 35%, rgba(193,229,240,1) 100%); */
    background-image: linear-gradient(to right, #02aabd, #52bcc8, #7acdd5, #9edfe2, #bff1f1);    
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    height: 70px    ;
   
 

}

.jcsb{
    justify-content: space-between;
}

.alcen{
    align-items: center;
}
.cart{
   font-size: 30px;
   color: black;
   cursor: pointer;
}

.cartvalue{
    border-radius: 50%;
    background-color: red;
    color: white;
    width: 20px;
    height: 20px;
    font-size: 15px !important;
    justify-content: center;
    align-items: center;
    display: flex;
    position: relative;
    left: -9px;
    top: -9px;
    font-weight: 600;
    cursor: pointer;
}