.leftnav{
    position: fixed;
    width: 15%;
    left: 0;

    background: rgb(192,235,237);
    background: linear-gradient(90deg, rgba(192,235,237,1) 0%, rgba(236,246,249,1) 100%);
    height: 100vh;
    padding-top: 20px;
    border-right: 1px solid #bbdee2;
    height: 100%;
}

.sidebar-menu{
    width: 80%;
    margin-left: 20px;
}

.leftnav p{
    padding:10px 10px;
    margin-top: 5px;
    font-weight: 600;
    color: hsl(232, 48%, 26%);
    letter-spacing: 0.2px;
}
.leftnav p:hover{
    background-color:#24a3b5;
    border-radius: 5px;
    color: white;
    transition: 0.3s;
    cursor: pointer;
}

.leftnav p:active{
 transform: scale(0.96);
}

.categ{
    text-align: center;
    color:  hsl(232, 48%, 26%);
}

@media (max-width:768px) {
    .leftnav{
        width: 20%;
    }
}
@media (max-width:430px) {
    .leftnav{
        width: 30%;
    }
}
