.pboxes{
    width: 90%;
    display: flex;
    position: absolute;
    right: 0px;
    left: 270px;
    max-width: 1200px;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 10px;
    max-width: 1250px;  
    margin: 0 auto;
    
}
.body1{
    margin-top: 70px; 
}

@media (max-width:768px) {
    .pboxes{
        left: 180px;
    }
}
@media (max-width:430px) {
    .pboxes{
        left: 180px;
        justify-content: center;
        align-items: center;
    }
}